import * as React from 'react';
import Col from 'antd/es/col';
import Context from './context';
class SearchFormItem extends React.Component {
    render() {
        let { label, col } = this.props;
        const { prefixCls, cols } = this.context;
        col = col < 1 ? 1 : col > cols ? cols : col;
        return (<Col span={col * (24 / cols)} className={`${prefixCls}-item`}>
                <div className={`${prefixCls}-item-label`}>
                    <label>{label}</label>
                </div>
                <div className={`${prefixCls}-item-control`}>{this.props.children}</div>
            </Col>);
    }
}
SearchFormItem.defaultProps = {
    col: 1,
};
SearchFormItem.contextType = Context;
export default SearchFormItem;
